"use strict";
//----------------------------------------
//  swiper
//----------------------------------------
export default class swiperGroup {
    constructor() {
        this.swipers = [];
    }

    //----------------------------------------
    //   : WORKS
    //----------------------------------------
    make_works( i_swiper, i_name ) {
        this.swipers[i_name] = new Swiper( i_swiper, {
            initialSlide: 2,
            loop: false,
            allowTouchMove: false,  //  
            //  
            centeredSlides: false, //
            speed: 500,
            effect: "fade",
            spaceBetween: 0,   //
            slidesPerView: 1,   //
            breakpoints: {      //
                599: { slidesPerView: 1 },
                1024: { slidesPerView: 1 },
            },
            updateOnWindowResize: true, //  
            autoplay : false
        });
    }


    //----------------------------------------
    //   : SKILL
    //----------------------------------------
    make_skill( i_swiper, i_name ) {
        // swiperslider
        this.swipers[i_name] = new Swiper( i_swiper, {
            loop: false,
            allowTouchMove: false,  //  
            //  
            pagination: {
                el: ".swiper-pagination",
                type: "bullets",
                clickable: "clickable"
            },

            centeredSlides: true, //
            spaceBetween: 16,   //16px
            slidesPerView: 1,   //2
            autoplay: false,
            breakpoints: {      //
                // 599px
                599: {
                    slidesPerView: 1, //2
                },
                // 1024px
                1024: {
                    slidesPerView: 1, //3
                },
            }
        });
    }


    //----------------------------------------
    //  swiper
    //----------------------------------------
    registSwiper( i_swiper, i_name ) {
        switch( i_name )
        {
        case "skill": this.make_skill( i_swiper, i_name );  break;
        case "works": this.make_works( i_swiper, i_name );  break;
        }
    }


    //----------------------------------------
    //  
    //----------------------------------------
    eventRegistration() {
        //  .swiper : swiper
        let swipergroup = document.querySelectorAll('.swiper');
        //  swiper
        swipergroup.forEach((swiper) => {
            let name = swiper.dataset.name;
            //  swiper
            if( name )
                this.registSwiper( swiper, name );
        });
    }
}
